import React from "react";
import MySelect from "./UI/select/MySelect";

const PostFilter = ({
    setPage,
    filter,
    setFilter,
    setPosts,
}) => {

    return (
        <div className="filter">
            <form id="filter">
                <MySelect
                    value={filter.ordering}
                    onChange={(selectedSort) => {
                        setPosts([]);
                        setFilter({
                            ...filter,
                            query: "",
                            ordering: selectedSort,
                            platforms: "",
                        });
                    }}
                    defaultValue=""
                    options={[
                        { value: "rating", name: " " },
                        { value: "released", name: "  " },
                    ]}
                />
                <MySelect
                    value={filter.orderByAscending}
                    onChange={(selectedOrder) => {
                        setPosts([]);
                        setFilter({
                            ...filter,
                            query: "",
                            platforms: "",
                            orderByAscending: selectedOrder,
                        });
                    }}
                    defaultValue=""
                    options={[
                        { value: "", name: " " },
                        { value: "-", name: " " },
                    ]}
                />
            </form>
        </div>
    );
};

export default PostFilter;
